<template>
  <div class="pullup-down-slide-wrapper" :style="`height:${height}`">
    <!-- pulldown -->
    <div class="pulldown-wrapper" ref="pulldown">
      <div v-show="beforePullDown">
        <span>Pull Down and refresh</span>
      </div>
      <div v-show="!beforePullDown">
        <div v-show="isPullingDown">
          <span>Loading...</span>
        </div>
        <div v-show="!isPullingDown">
          <span>Refresh success</span>
        </div>
      </div>
    </div>
    <div class="pullup-pulldown-slide-bswrapper" ref="bsWrapper">
      <div class="pullup-pulldown-slide-scroller">
        <!-- slide item -->
        <div
          :key="item.vodrow.vodid"
          class="pullup-pulldown-slide-item"
          :class="{ ['page' + (idx % 4)]: true }"
          v-for="(item, idx) of rows"
          @click="itemClick(item)"
        >
          <div style="line-height: initial; font-size: initial">
            <div>
              <span>{{ item.vodrow.title }}</span>
              <span>/</span>
              <span>{{ item.vodrow.scorenum }}</span>
              <span>/</span>
              <span>{{ item.vodrow.playcount_total }}</span>
              <span>/</span>
              <span>{{ item.vodrow.downcount_total }}</span>
            </div>
            <div>
              <span>@{{ item.user.nickname }}</span>
              <span>/</span>
              <span>{{ item.vodrow.updatetime }}</span>
            </div>
          </div>
          <video
            v-if="Math.abs(pageY - idx) <= 1"
            class="video"
            :id="item.vodrow.vodid"
            controls="controls"
            ref="video"
            playsinline
            :autoplay="pageY == idx"
            width="100%"
            :src="item.videoUrl"
            :poster="item.vodrow && item.vodrow.coverpic"
          ></video>
          <van-image
            fit="cover"
            width="100%"
            height="100%"
            :src="item.vodrow.coverpic"
            v-else
          />
        </div>
      </div>
    </div>
    <!-- pollup -->
    <div class="pullup-wrapper" ref="pullup">
      <div v-show="!isPullingUp">
        <span>Pull Up and load</span>
      </div>
      <div v-show="isPullingUp">
        <span>Loading...</span>
      </div>
    </div>
  </div>
</template>

<script>
const BASE = 2;
const TIME_BOUNCE = 700;
const REQUEST_TIME = 1000;
const THRESHOLD = 50;
const STOP = 56;
import config from "../js/config.js";

export default {
  data() {
    return {
      beforePullDown: true,
      isPullingDown: false,
      isPullingUp: false,
      dataList: new Array(BASE),
      stopPullup: false,
      height: "100vh",
      pageY: 0,
      rows: [],
      bscroll: null,
    };
  },
  computed: {
    currentItem() {
      return this.rows.length ? this.rows[this.pageY] : null;
    },
  },
  watch: {
    pageY() {
      this.videoUrlUpdate();
      if (this.pageY == this.rows.length - 2) {
        this.loadData(false);
      }
    },
    rows() {
      this.videoUrlUpdate();
    },
  },
  created() {
    this.height = window.innerHeight + "px";
    this.bscroll = null;
    this.loadData(true);
    window.onresize = () => {
      this.height = window.innerHeight + "px";
      this.$nextTick(() => {
        this.bscroll.refresh();
      });
    };
  },
  mounted() {
    this.initBscroll();
  },
  methods: {
    async itemClick(item) {
      // let res = await this.$axios.request({
      //   url: config.origin + item.vodrow.play_url,
      //   params: config.query(),
      // });
      // item.videoUrl = res.data.data.httpurl || res.data.data.httpurl_preview;
      // this.currentItem = this.currentItem == item ? null : item;
    },
    loadData(isFirst) {
      return this.$axios
        .request({
          url: config.origin + "/minivod/reqlist",
          params: config.query(),
        })
        .then((res) => {
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            var rows = [];
            res.data.data.rows.map((item) => {
              if (item.vodrow && item.vodrow.vodid) {
                item.videoUrl = "";
                rows.push(item);
              }
            });
            if (isFirst) {
              this.rows = rows;
              // this.changePage(0);
            } else {
              this.rows.push(...rows);
            }
            this.$nextTick(() => {
              this.initBscroll();
              this.bscroll.refresh();
            });
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch((res) => {
          this.isMoreLoading = false;
          weui.topTips(res.message || "fail");
        });
    },
    videoUrlUpdate() {
      document.title = this.rows[this.pageY].vodrow.title;
      // vant.Notify(this.rows[this.pageY].vodrow.title);
      this.$nextTick(() => {
        this.$refs.video.forEach((video) => {
          if (video.id == this.currentItem.vodrow.vodid) {
            video.play();
          } else {
            video.pause();
          }
        });
      });
      this.rows
        .filter((item, i) => Math.abs(this.pageY - i) <= 1)
        .forEach((item) => {
          if (!item.videoUrl) {
            this.$axios
              .request({
                url: config.origin + item.vodrow.play_url,
                params: config.query(),
              })
              .then((res) => {
                item.videoUrl =
                  res.data.data.httpurl || res.data.data.httpurl_preview;
              });
          }
        });
    },
    initBscroll() {
      if (this.bscroll || !this.rows.length) {
        return;
      }
      this.bscroll = BetterScroll.createBScroll(this.$refs.bsWrapper, {
        scrollY: true,
        bounceTime: TIME_BOUNCE,
        momentum: false,
        click: true,
        // pullDown options
        pullDownRefresh: {
          threshold: THRESHOLD,
          stop: STOP,
        },
        // pullUp options
        pullUpLoad: {
          threshold: -THRESHOLD,
        },
        // slide options
        slide: {
          threshold: 5,
          disableSetHeight: true,
          autoplay: false,
          loop: false,
        },
      });
      window.bscroll = this.bscroll;
      // listening evnets
      this.bscroll.on("pullingDown", this.pullingDownHandler);
      this.bscroll.on("pullingUp", this.pullingUpHandler);
      this.bscroll.on("scroll", this.scrollHandler);
      this.bscroll.on("slideWillChange", this.slideWillChange);
    },
    slideWillChange({ pageY }) {
      this.pageY = pageY;
    },
    // scroll event handler
    scrollHandler(pos) {
      if (pos.y >= 0) {
        const pullDownEle = this.$refs.pulldown;
        const { height: pulldownH } = getComputedStyle(pullDownEle, null);
        pullDownEle.style.transform = `translateY(${
          -parseInt(pulldownH) + pos.y
        }px) translateZ(0)`;
      }
      const pullupThreshold = -30;
      const maxScrollY = this.bscroll.maxScrollY;
      if (pos.y - maxScrollY <= pullupThreshold && this.isPullingUp) {
        this.stopPullup = true;
      }
      if (pos.y - maxScrollY <= 0 && !this.stopPullup) {
        const pullUpEle = this.$refs.pullup;
        const { height: pullupH } = getComputedStyle(pullUpEle, null);
        pullUpEle.style.transform = `translateY(${
          pos.y - maxScrollY
        }px) translateZ(0)`;
      }
    },
    // pullingDown event handler
    async pullingDownHandler() {
      this.beforePullDown = false;
      this.isPullingDown = true;
      this.bscroll.enabled = false;
      // await this.requestData("refresh");
      await this.loadData(true);
      this.isPullingDown = false;
      this.$nextTick(() => {
        this.bscroll.finishPullDown();
        this.beforePullDown = true;
        this.bscroll.enabled = true;
        this.bscroll.refresh();
      });
    },
    // pullingUp event handler
    async pullingUpHandler() {
      this.isPullingUp = true;
      this.bscroll.enabled = false;
      // await this.requestData("load");
      await this.loadData(false);
      this.$nextTick(() => {
        this.isPullingUp = false;
        this.bscroll.finishPullUp();
        this.bscroll.enabled = true;
        this.bscroll.refresh();
        this.resetPullupPos();
      });
    },
    async requestData(type) {
      try {
        const newData = await this.ajaxGet(/* url */);
        if (type === "load") {
          this.dataList = newData.concat(this.dataList);
        } else {
          this.dataList = newData;
        }
      } catch (err) {
        // handle err
        console.log(err);
      }
    },
    ajaxGet(/* url */) {
      return new Promise((resolve) => {
        setTimeout(() => {
          const dataList = new Array(BASE);
          resolve(dataList);
        }, REQUEST_TIME);
      });
    },
    resetPullupPos() {
      const pullUpEle = this.$refs.pullup;
      pullUpEle.style.transform = `translateY(0px) translateZ(0)`;
      pullUpEle.style.transition = "transform 0.5s";
      this.stopPullup = false;
    },
  },
};
</script>

