  height: 100vh;
  overflow: hidden;
}

.pullup-pulldown-slide-bswrapper {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.pullup-down-list {
  padding: 0;
}

.pullup-pulldown-slide-item {
  list-style: none;
  // border-bottom 1px solid #ccc
  width: 100%;
  line-height: 200px;
  text-align: center;
  font-size: 26px;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  box-sizing: border-box;
}

.pulldown-wrapper {
  position: absolute;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  transform: translateY(-100%) translateZ(0);
  text-align: center;
  color: #999;
}

.pullup-wrapper {
  height: 40px !important;
  padding: 20px;
  text-align: center;
  color: #999;
}

